@font-face {
  font-family: "OpenSans", "Arial", sans-serif;
  src: local("OpenSans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}
.displaynone {
  display: none;
}
body {
  width: 100%;
  min-width: 330px;
  padding: 0;
