}
.header__menu-toggle {
  position: relative;
  display: block;
  background-color: transparent;
  border: none;
  margin: 20px 20px 0 auto;
  height: 25px;
  width: 50px;
  outline: none;
}
@media (min-width: 660px) {
  .header__menu-toggle {
    margin: 44px 20px 0 auto;
  }
}
@media (min-width: 960px) {
  .header__menu-toggle {
    display: none;
  }
}
.header__menu-toggle::before {
  position: absolute;
  content: "";
  display: block;
  width: 50px;
  height: 4px;
  left: 0;
  top: 0;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.header__menu-toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.header__menu-toggle:active::before {
  background-color: rgba(210, 40, 86, 0.7);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.7), 0 20px 0 0 rgba(210, 40, 86, 0.7);
}
.header__menu-toggle--opened {
  position: relative;
  margin-right: 7px;
  z-index: 2;
}
@media (min-width: 660px) {
  .header__menu-toggle--opened {
    margin-right: -18px;
    margin-top: 53px;
  }
}
.header__menu-toggle--opened::before {
  z-index: 2;
  content: "";
  position: absolute;
  top: 10px;
  left: 7px;
  width: 30px;
  height: 5px;
  transform: rotate(45deg);
  background-color: #ffffff;
  box-shadow: none;
}
@media (min-width: 660px) {
  .header__menu-toggle--opened::before {
    top: 0;
    left: 0;
  }
}
.header__menu-toggle--opened::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 7px;
  width: 30px;
  height: 5px;
  transform: rotate(-45deg);
  background-color: #ffffff;
}
@media (min-width: 660px) {
  .header__menu-toggle--opened::after {
    top: 0;
    left: 0;
  }
}
.header__menu-toggle--opened:hover::before,
.header__menu-toggle--opened:hover::after {
  background-color: #d22856;
  box-shadow: none;
}
.header__menu-toggle--opened:active::before,
.header__menu-toggle--opened:active::after {
  box-shadow: none;
  background-color: rgba(210, 40, 86, 0.7);
}
.header__menu-toggle--nojs {
  display: none;
}
